y:scroll;width:33.2rem}.completed{text-decoration:line-through;-webkit-transform:scale(0.9);transform:scale(0.9)}.content--todo{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;min-height:5rem;-webkit-justify-content:flex-start;justify-content:flex-start;margin:.5rem 0;overflow-y:scroll;width:33.2rem}.content--todo p{margin:0;border-radius:1rem;box-shadow:0px 3px 6px #00000029;background:#FFFFFF 0% 0% no-repeat padding-box;color:#888888;font-size:1.6rem;font-family:Georgia, 'Times New Roman', Times, serif;height:6.5rem;line-height:1.5;padding:1.5rem 1.4rem 1.2rem 1.4rem;overflow-y:scroll;overflow-wrap:break-word;text-align:left;scroll-bar-width:0px;width:32rem}.content--todo div{text-align:right;width:100%;margin-top:.8rem}.content--todo div:last-child{margin-right:1rem}.content--todo i{font-size:1.8rem;margin:.5rem;transition:.3s}.section--input{-webkit-align-self:baseline;align-self:baseline;-webkit-align-items:center;align-items:center;background-color:#FCD9D9;box-shadow:0px 3px 6px #00000029;display:-webkit-flex;display:flex;height:6.2rem;-webkit-justify-content:flex-start;justify-content:flex-start;margin-top:2.7rem;position:relative;width:33.1rem}.section--input input{background-color:#FCD9D9;border:1px solid transparent;color:#3b3b3b;font-size:1.6rem;font-family:Georgia, 'Times New Roman', Times, serif;height:4rem;margin-top:1rem;padding-left:1.5rem;outline:none;width:26rem}.section--input button{color:#e9e9e9;display:block;background-color:#f48888;border-radius:50%;box-shadow:0px 3px 6px #00000029;font-size:3.2rem;height:9rem;position:absolute;outline:none;right:-3rem;transition:.3s;width:9rem}.section--input button:active{-webkit-transform:translateY(0.3rem);transform:translateY(0.3rem)}

